body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-dm-sans: "DM Sans";

  /* font sizes */
  --font-size-lg: 18px;
  --font-size-13xl: 32px;
  --font-size-lgi: 19px;
  --font-size-7xl: 26px;

  /* Colors */
  --color-black: #000;
  --color-white: #fff;

  /* Gaps */
  --gap-xl: 20px;
  --gap-xl-7: 20.7px;
  --gap-3xs: 10px;
  --gap-23xl-8: 42.8px;
  --gap-2xl: 21px;
  --gap-8xs-6: 4.6px;

  /* Paddings */
  --padding-46xl: 65px;
  --padding-lgi-9: 19.9px;
  --padding-18xl: 37px;
  --padding-xl: 20px;
  --padding-4xl: 23px;
  --padding-mini: 15px;
  --padding-lg: 18px;
  --padding-104xl: 123px;
  --padding-mid: 17px;
  --padding-33xl: 52px;
  --padding-32xl: 51px;
  --padding-38xl: 57px;
  --padding-37xl: 56px;
  --padding-110xl: 129px;
  --padding-xl-6: 20.6px;
  --padding-2xl: 21px;
  --padding-5xs-8: 7.8px;
  --padding-mini-3: 14.3px;
  --padding-base: 16px;
  --padding-xs: 12px;
  --padding-3xs: 10px;

  /* Border radiuses */
  --br-23xl: 42px;
  --br-15xl: 34px;
  --br-17xl: 36px;
}
